import React from "react";
import { Link } from "react-router-dom";
import rooms from "rooms";

const Nav = () => {
  return (
    <aside className="Nav">
      <nav>
        <ul>
          {rooms.map((room, key) => (
            <li key={key}>
              <Link to={room.slug}>{room.title}</Link>
            </li>
          ))}
          <li>
            <Link to="/ybor">Ybor</Link>
          </li>
        </ul>
      </nav>
    </aside>
  );
};

export default Nav;
